import React, { useEffect, useRef } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import missionImg from '../assets/mission.jpg';
import visionImg from '../assets/vision.jpg';
import founderImg from '../assets/founder.jpeg';
import faqLogo from '../assets/faq.jpg';

gsap.registerPlugin(ScrollTrigger);

const About = () => {
  const sectionRef = useRef(null);

  useEffect(() => {
    const items = sectionRef.current.querySelectorAll('.about-item');

    items.forEach((el, i) => {
      gsap.fromTo(
        el,
        { opacity: 0, y: 50 },
        {
          opacity: 1,
          y: 0,
          duration: 1,
          delay: i * 0.2,
          scrollTrigger: {
            trigger: el,
            start: 'top 80%',
            toggleActions: 'play none none reverse',
          },
        }
      );
    });
  }, []);

  return (
    <div className="section container" ref={sectionRef} style={{ fontFamily: `'Playfair Display', Georgia, serif` }}>
      {/* Heading */}
      <h2 className="text-golden about-item mb-4" style={{ fontSize: '3rem' }}>About Company</h2>
      <p className="about-item" style={{ fontSize: '1.2rem', lineHeight: '1.8' }}>
        We are a forward-thinking financial firm committed to making credit and investments accessible for all.
        Through strategic EMI solutions and personalized planning, we aim to empower every household with
        financial confidence. Our approach is built on trust, transparency, and tailored guidance to help
        our clients reach their financial goals with ease.
      </p>

      {/* Mission */}
      <div className="about-item d-flex flex-column flex-md-row align-items-center mt-5">
        <img
          src={missionImg}
          alt="Mission"
          style={{ width: '350px', height: '350px', objectFit: 'cover', borderRadius: '10px' }}
          className="mb-3 mb-md-0 me-md-4"
        />
        <div>
          
          <p style={{ fontSize: '1.2rem' }}>
            To empower our clients and their families to achieve financial independence and maximize the utility
            of their wealth, enabling them to live with confidence, purpose, and peace of mind. We strive to be a
            trusted partner in every stage of life, offering clear guidance, personalized strategies, and unwavering
            support.
          </p>
        </div>
      </div>

      {/* Vision - reverse layout */}
      <div className="about-item d-flex flex-column flex-md-row align-items-center mt-5">
        <div className="order-md-2 ms-md-4 mb-3 mb-md-0">
          <img
            src={visionImg}
            alt="Vision"
            style={{ width: '350px', height: '350px', objectFit: 'cover', borderRadius: '10px' }}
          />
        </div>
        <div className="order-md-1">
          <p style={{ fontSize: '1.2rem' }}>
            Our vision is to empower society at large to achieve financial independence through accessible,
            reliable, and personalized financial support and education.
          </p>
        </div>
      </div>

      {/* From the Founder */}
      <div className="about-item d-flex flex-column flex-md-row align-items-center mt-5">
        <img
          src={founderImg}
          alt="Founder"
          style={{
            width: '350px',
            height: '350px',
            objectFit: 'cover',
            borderRadius: '50%',
            border: '4px solid #d4af37',
          }}
          className="mb-3 mb-md-0 me-md-4"
        />
        <div>
          <h4 className="text-golden mb-2" style={{ fontSize: '2rem' }}>From the Founder</h4>
          <p style={{ fontSize: '1.2rem' }}>
            "We believe in a better future through finance. Everyone should have the opportunity
            to understand and control their financial destiny. That belief is the heart of everything we do."
          </p>
        </div>
      </div>

      {/* FAQs Section */}
      <div className="about-item mt-5 pt-5">
        <div className="text-center mb-4">
          <img
            src={faqLogo}
            alt="FAQ Logo"
            style={{ width: '260px', height: '200px' }}
          />
        </div>

        {/* Accordion */}
        <div className="accordion" id="faqAccordion">
          {[
            { q: "How do I apply for EMI?", a: "Click on “Services” and select the desired plan." },
            { q: "Are services secure?", a: "Yes, we use bank-grade encryption and security protocols." },
            { q: "What is the minimum investment I can start with?", a: "As low as ₹500/month for SIPs." },
            { q: "Can I cancel my plan anytime?", a: "Yes, most of our plans are flexible and can be stopped with notice." },
            { q: "Is financial consultation available?", a: "Yes, expert guidance is available upon request." },
            { q: "Do you offer services in regional languages?", a: "Currently available in English and Hindi, with more coming soon." },
          ].map((item, index) => (
            <div className="accordion-item mb-3" key={index}>
              <h2 className="accordion-header" id={`heading${index}`}>
                <button
                  className="accordion-button collapsed"
                  type="button"
                  data-bs-toggle="collapse"
                  data-bs-target={`#collapse${index}`}
                  aria-expanded="false"
                  aria-controls={`collapse${index}`}
                >
                  <strong>{item.q}</strong>
                </button>
              </h2>
              <div
                id={`collapse${index}`}
                className="accordion-collapse collapse"
                aria-labelledby={`heading${index}`}
                data-bs-parent="#faqAccordion"
              >
                <div className="accordion-body" style={{ fontWeight: 'normal', fontSize: '1.1rem' }}>
                  {item.a}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default About;
